import { Link } from 'gatsby';
import React from 'react'
import { Column } from '../styles/StyledFlexComponents'

// atom
export default function ProjectCard(props){
    const {date, linkTo,titleImage, title, description, usedSkills} = props;
    // const {date, category, title, description, iconifyIconName, linkTo} = props;
    return (
        <Link to={linkTo}>
            <Column className='projectCard'
                style={{paddingBottom:"1em"}}>
                <img src={titleImage} width="100%" style={{maxHeight: 400, objectFit: 'contain'}}/>
                <Column style={{textAlign: "center", padding: 20}}>
                    <h3 style={{marginBlock: 10}}>{title}</h3>
                    {/* <strong>e-cumerse</strong> */}
                    <div>{description}</div>
                    <div>{date}</div>
                </Column>
            </Column>
        </Link>
    )
}
